import React, { useState } from "react";
import Persons from "./components/person/Persons";
import { ToastContainer, toast } from "react-toastify";
import Header from "./components/common/Header";
import SimpleContext from "./context/SimpleContext";
import NewPerson from "./components/person/NewPerson";

const App = () => {
  const [getPersons, setPersons] = useState([]);
  const [getSinglePerson, setSinglePerson] = useState("");
  const [getShowPersons, setShowPersons] = useState(true);

  const handleShowPersons = () => {
    setShowPersons(!getShowPersons);
  };

  const handleDeletePerson = (id) => {
    const persons = [...getPersons];
    const filteredPersons = persons.filter((p) => p.id !== id);
    setPersons(filteredPersons);
    toast.error("عملیات حذف انجام شد!", {
      position: "top-right",
      autoClose: true,
    });
  };
  const handleNameChange = (event, id) => {
    const allPersons = [...getPersons];
    const personIndex = allPersons.findIndex((p) => p.id === id);
    const person = allPersons[personIndex];
    person.fullName = event.target.value;
    const persons = [...allPersons];
    persons[personIndex] = person;
    setPersons(persons);
  };

  const handleNewPerson = () => {
    const persons = [...getPersons];
    const person = {
      id: Math.floor(Math.random() * 1000),
      fullName: getSinglePerson,
    };
    if (person.fullName !== "" && person.fullName !== " ") {
      persons.push(person);
      setPersons(persons);
      setSinglePerson("");
      toast.success("شخص جدید اضافه شد.", {
        position: "bottom-right",
        autoClose: true,
        closeOnClick: true,
      });
    }
  };

  const setPerson = (event) => {
    setSinglePerson(event.target.value);
  };

  let person = null;
  if (getShowPersons) {
    person = <Persons />;
  }
  return (
    <SimpleContext.Provider
      value={{
        persons: getPersons,
        person: getSinglePerson,
        handleDeletePerson: handleDeletePerson,
        handleNameChange: handleNameChange,
        handleNewPerson: handleNewPerson,
        setPerson: setPerson,
      }}
    >
      <div className="rtl text-center">
        <Header appTitle="مدیریت کننده اشخاص" />
        <NewPerson />
        <button
          className={getShowPersons ? "btn btn-info" : "btn btn-danger"}
          onClick={handleShowPersons}
        >
          {getShowPersons ? "عدم نمایش" : "نمایش"} اشخاص
        </button>
        {person}
        <ToastContainer />
      </div>
    </SimpleContext.Provider>
  );
};

export default App;
