import React from "react";

const Person = ({ fullName, personDeleted, handleNameChange }) => {
  return (
    <div className="card text-white w-25 mt-3 mb-3 bg-info mx-auto">
      <div className="card-body text-center">
        <p className="d-block">{`${fullName}`}</p>
        <div className="input-group justify-content-center">
          <input
            type="text"
            onChange={handleNameChange}
            className="form-control"
            placeholder={fullName}
          />
          <div className="input-group-prepend">
            <button
              className="btn btn-sm btn-danger fa fa-trash"
              onClick={personDeleted}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Person;
